  height: 64px;
  z-index: 100;
  position: fixed;
  left: 0;
  right: 0;
}

@media screen and (max-width: 767.98px) {
  .page-header {
    -webkit-backdrop-filter: blur(2px);
    backdrop-filter: blur(2px);
  }
}

